<template>
  <div class="hello">
    <el-button @click="btnClick">按钮</el-button>
    <echarts-test></echarts-test>
  </div>
</template>

<script>
import echartsTest from '@/components/echarts/echartsTest.vue'
export default {
  name: 'IndexPage',
  data() {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted() {
    let obj = { key: "" }
    console.log(Object.keys(obj).length, 999999999)
  },
  methods: {
    btnClick() {
      let _this = this;
      const data = JSON.stringify({
        action: 'LOGIN',
        username: "H5页面上送的用户名",
        password: "H5页面上送的用户密码"
      });
      debugger
      console.log("---------------------------------按钮点击---------------------------------------")
      _this.$bridge.callHandler('login', data, function (responseData) {
        console.log("---------------------------------回调进来---------------------------------------")
          const response = JSON.parse(responseData);
          if (response.success) {
            alert("登录成功: " + response.message);
          } else {
            alert("登录失败: " + response.message);
          }
      })
    }
  },
  components: {
    echartsTest
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

.prosson {
  height: 20px;
  width: 200px;
  border-radius: 20%;
}

.babackgroundddd {
  height: 20px;
  width: 100px;
  border: 1px solid #42b983;
  border-radius: 20%;
  background-color: #42b983;
}
</style>



